"use client"

import { useState, useEffect } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Sparkles, Moon, Play, Square } from "lucide-react"

interface MonthCalendarProps {
  month: string
  days: number
  year: number
  userEmail: string
  monthIndex: number
}

export function MonthCalendar({ month, days, year, userEmail, monthIndex }: MonthCalendarProps) {
  const [readings, setReadings] = useState<any[]>([])
  const [tempBookNames, setTempBookNames] = useState<Record<number, string>>({})

  const fetchReadings = async () => {
    try {
      const res = await fetch(`/api/reading-data?email=${userEmail}`)
      const data = await res.json()
      setReadings(data.data || [])
    } catch (error) {
      console.error("Erro ao buscar leituras:", error)
    }
  }

  useEffect(() => {
    if (userEmail) fetchReadings()
  }, [userEmail, monthIndex, year])

  const handleAction = async (day: number, action: "START_READING" | "FINISH_READING", bookNameFromCard?: string) => {
    const bookName = action === "START_READING" ? tempBookNames[day] : bookNameFromCard
    
    if (!bookName) {
      alert("Por favor, digite o nome do livro.")
      return
    }

    try {
      const res = await fetch("/api/reading-data", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ email: userEmail, bookName, action }),
      })

      if (res.ok) {
        if (action === "FINISH_READING") setTempBookNames({})
        fetchReadings()
      }
    } catch (error) {
      console.error("Erro na ação:", error)
    }
  }

  return (
    <Card className="border-accent/20 bg-card p-6 shadow-lg md:p-10">
      <div className="mb-8 text-center">
        <div className="mb-2 flex items-center justify-center gap-2">
          <Sparkles className="h-5 w-5 text-primary" />
          <h2 className="font-serif text-3xl font-bold text-foreground md:text-4xl">{month}</h2>
          <Sparkles className="h-5 w-5 text-primary" />
        </div>
        <p className="text-lg text-muted-foreground">{year}</p>
      </div>

      <div className="grid grid-cols-2 gap-3 md:grid-cols-7 md:gap-4">
        {Array.from({ length: days }, (_, i) => i + 1).map((day) => {
          const currentDate = new Date(year, monthIndex, day)
          currentDate.setHours(0, 0, 0, 0)

          const activeReading = readings.find((r) => {
            const start = new Date(r.start_date)
            start.setHours(0, 0, 0, 0)
            const end = r.end_date ? new Date(r.end_date) : new Date()
            end.setHours(23, 59, 59, 999)
            return currentDate >= start && currentDate <= end
          })

          const isLendo = activeReading && activeReading.status === "lendo"
          const currentDisplayBook = activeReading?.book_name || tempBookNames[day] || ""

          return (
            <div
              key={day}
              className={`group relative flex min-h-[180px] flex-col rounded-lg border p-3 transition-all ${
                isLendo ? "border-green-500 bg-green-50/50 shadow-sm" : "border-border bg-background/50"
              }`}
            >
              <div className="mb-2 flex items-center justify-between">
                <span className={`font-bold ${isLendo ? "text-green-700" : "text-primary"}`}>{day}</span>
                {day % 7 === 0 && <Moon className="h-3 w-3 text-muted-foreground/40" />}
              </div>

              <div className="flex flex-col gap-2 mt-auto">
                <Button
                  variant="outline"
                  size="sm"
                  disabled={!!activeReading}
                  className="h-7 w-full text-[9px] font-bold uppercase hover:bg-green-600 hover:text-white disabled:opacity-30"
                  onClick={() => handleAction(day, "START_READING")}
                >
                  <Play className="mr-1 h-3 w-3" /> Iniciar
                </Button>

                <Button
                  variant="outline"
                  size="sm"
                  disabled={!isLendo}
                  className={`h-7 w-full text-[9px] font-bold uppercase ${isLendo ? "border-red-200 text-red-600 hover:bg-red-600 hover:text-white" : "disabled:opacity-30"}`}
                  onClick={() => handleAction(day, "FINISH_READING", activeReading?.book_name)}
                >
                  <Square className="mr-1 h-3 w-3" /> Encerrar
                </Button>

                <Input
                  placeholder="Título do livro"
                  value={currentDisplayBook}
                  disabled={!!activeReading}
                  onChange={(e) => setTempBookNames(prev => ({ ...prev, [day]: e.target.value }))}
                  className={`h-7 text-[10px] ${isLendo ? "bg-white border-green-200" : "bg-background/80"}`}
                />
              </div>
            </div>
          )
        })}
      </div>

      <div className="mt-8 flex items-center justify-center gap-2 text-xs text-muted-foreground">
        <div className="h-px w-12 bg-border" />
        <span>✦</span>
        <div className="h-px w-12 bg-border" />
      </div>
    </Card>
  )
}