"use client"

import { useState, useEffect } from "react"
import { useSession } from "next-auth/react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { BookOpen, CheckCircle2, Play } from "lucide-react"

export function ReadingTracker() {
  const { data: session } = useSession()
  const [bookName, setBookName] = useState("")
  const [isReading, setIsReading] = useState(false)

  useEffect(() => {
    if (session?.user?.email) {
      fetch(`/api/reading-data?email=${session.user.email}`)
        .then(res => res.json())
        .then(data => {
          const active = data.data?.find((r: any) => r.status === 'lendo')
          if (active) {
            setIsReading(true)
            setBookName(active.book_name)
          }
        })
    }
  }, [session])

  const handleToggle = async () => {
    if (!bookName) return
    
    const action = isReading ? "FINISH_READING" : "START_READING"
    const res = await fetch("/api/reading-data", {
      method: "POST",
      body: JSON.stringify({ 
        email: session?.user?.email, 
        bookName, 
        action 
      }),
    })

    if (res.ok) {
      setIsReading(!isReading)
      if (isReading) setBookName("")
      window.location.reload()
    }
  }

  return (
    <div className="flex flex-col gap-4 p-6 bg-white rounded-xl border shadow-sm mb-8">
      <div className="flex items-center gap-2 text-primary">
        <BookOpen className="h-5 w-5" />
        <h2 className="text-lg font-bold font-serif">Controle de Leitura Atual</h2>
      </div>
      
      <div className="flex gap-3">
        <Input 
          placeholder="Qual livro você vai começar a ler?"
          value={bookName}
          onChange={(e) => setBookName(e.target.value)}
          disabled={isReading}
          className="flex-1 bg-slate-50"
        />
        <Button 
          onClick={handleToggle}
          variant={isReading ? "destructive" : "default"}
          className="gap-2 min-w-[140px]"
        >
          {isReading ? (
            <><CheckCircle2 className="h-4 w-4" /> Finalizar</>
          ) : (
            <><Play className="h-4 w-4" /> Iniciar</>
          )}
        </Button>
      </div>
    </div>
  )
}